export default {
    name: "TopBar",
    props: {
        title: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            sex: true,
            modalName: null,
            modalList: [
                {key: 'delete', value: '书籍管理'},
                {key: 'list', value: '收藏模式'},
                {key: 'down', value: '缓存管理'}
            ]
        }
    },
    methods: {
        changeSex(e) {
            this.sex = e.detail.value;
        },
        showModal(e) {
            this.modalName = e.currentTarget.dataset.target
        },
        hideModal(e) {
            this.modalName = null
        },
        routeTo() {
            uni.navigateTo({
                url: '/pages/search/Search',
                success: res => {},
                fail: () => {},
                complete: () => {}
            });
        }
    }
}
